@import "../../../scss/fonts.scss";
@import "../../../scss/colors.scss";
@import "../../../components/logo/logo.scss";
@import "../../../components/header/header.scss";
@import "../../../components/footer-creative/footer-creative.scss";
@import "../../../components/footer/footer.scss";

html {
  font-size: 16px;
  font-family: $font-main;
}

body {
  margin: 0;
  background: #F4F4F6;
}

.logo {
  max-width: 1380px;
  margin: 0 auto;
  padding: 0 30px;
  @media (max-width: 1440px) {
    padding-left: 2.1vw;
  }
  &__wrapper{
    position: absolute;
    top: 30px;
  }
}
.headers-footers {
  &__header{
    margin-top: 150px;
    &_user{
      margin-top: 40px;
    }
  }
  &__footer{
    margin-top: 40px;
  }
  &__footer-creative{
    margin-top: 40px;
    margin-bottom: 130px;
  }
}